import tabBar from '../../common/tabbar.vue';
	
export default {
    data() {
        return {
			height: 0,
			isIphoneX: false,
        }
    },
	onLoad() {
		let winHeight      = uni.getSystemInfoSync().windowHeight;
		this.height = winHeight -82;
		this.isIphoneX = getApp().globalData.isIphoneX;
		if(this.isIphoneX) {
			this.height = this.height - 34;
		}	
	},
	mounted() {
		
	},
	components: {
		tabbar: tabBar
	},
    methods: {  
		preview() {
			
		},
		navigate() {
			
		}
    }
}
